class Item extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const {
            id,
            name,
            image,
            category,
            label,
            price
        } = this.props;
        return(
            <div className="item-container">
                <div className="item-img">
                    <img src={ image } width="149" />
                    <img src="assets/images/heart.svg" className="heart"/>
                    <div className="shop-now">Mua ngay</div>
                </div>
                <div className="item-info">
                    <p className="item-name">{ name }</p>
                    <p className="item-price">{ price }</p>
                </div>
            </div>
        );
    }
}


class Sneakers extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const result = sneakerItems.map(item =>
            <Item id={item.id} name={item.name} image={item.image} category={item.name} label={item.label} price={item.price} />                    
        );
        return(
            <div className="category-container">
                <div className="category-header">
                    <span />
                    <p class="category-name">SNEAKERS</p>
                </div>
                {
                    result
                }
            </div>
        );
    }
}

