import React, { Component } from 'react';
import './Header.css';

class Header extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="header-container">
                <h1 className="store-name">ABE SHOES</h1>
                <p className="store-logan">Đồng hành cùng bạn!</p>
            </div>
        );
    }
}

export default Header;