import React, { Component } from 'react';
import './Hot.css';
import $ from 'jquery';

let hotItems = [
    {
        id: 21,
        name:'Sneaker 001',
        image: 'assets/images/21.jpg',
        category: 'sneaker',
        label:'New',
        price:'1.690.000đ',
        active: true
    },
    {
        id: 26,
        name:'Sneaker 002',
        image: 'assets/images/26.jpg',
        category: 'sneaker',
        label:'New',
        price:'1.390.000đ',
        active: false
    },
    {
        id: 34,
        name:'Sneaker 003',
        image: 'assets/images/34.jpg',
        category: 'sneaker',
        label:'New',
        price:'1.090.000đ',
        active: false
    },
    {
        id: 35,
        name:'Sneaker 004',
        image: 'assets/images/35.jpg',
        category: 'sneaker',
        label:'New',
        price:'1.990.000đ',
        active: false
    }
];

let active = hotItems[0];

class HotItems extends Component {
    constructor(props) {
        super(props);
        
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        console.log('this.props.updateDisplay', this.props.updateDisplay);
        for (let i in hotItems) {
            hotItems[i].active = (hotItems[i].id == this.props.item.id) ? true : false;
        }
        this.props.updateDisplay(this.props.item);
    }

    render() {
        const {item} = this.props;
        return(
            <img src={item.image} className="hot-image" onClick={this.handleClick} />
        );
    }
}

class Hot extends Component {
    constructor(props) {
        super(props);
        this.state = {
            itemActive: hotItems[0],
            result: ''
        }
        this.updateDisplay = this.updateDisplay.bind(this);
    }

    updateDisplay(item) {
        this.setState({
            itemActive: item,
        });
        this.setState({
            result: hotItems.map(item => {
                return (item.active) ?
                (
                    <div className="hot-item">
                        <HotItems item={item} updateDisplay={this.updateDisplay} />
                        <img src="assets/images/selected.svg" className="selected" />
                    </div>
                ) :
                (
                    <div className="hot-item">
                        <HotItems item={item} updateDisplay={this.updateDisplay} />
                    </div>
                );
            })
        });       
    }

    componentWillMount() {  
        this.updateDisplay(hotItems[0]);
    }

    render() {
        return(
            <div className="hot-products">
                {this.state.result}
                <img className="hot-item active" src={this.state.itemActive.image} />
            </div>
        );
    }
    
}

export default Hot;